<template>
  <div id="newbillDetail">
    <van-nav-bar class="top"
      fixed
      title="运单详情"
      left-text="返回"
      right-text="修改运单"
      left-arrow
      @click-left="goBack"
      @click-right="changeNewBill"
    />
    <van-steps :active="activeStep" active-color="#38f">
      <van-step v-for="i in stepArr" :key="i.id">
        <span @click="setStep(i.id)">{{i.stepName}}</span>
      </van-step>
    </van-steps>
    <van-tabs v-model="activeTab" swipeable animated class="tab-top">
      <van-tab title="发货人" class="infoGroup">
        <div class="infoItem">
          <span class="field">运单</span>
          <span class="info">{{title}}</span>
        </div>
        <div class="infoItem">
          <span class="field">托运人</span>
          <span class="info">{{shipper_name}}</span>
        </div>
        <div class="infoItem">
          <span class="field">手机号</span>
          <span class="info">{{shipper_phone}}</span>
        </div>
        <div class="infoItem">
          <span class="field">发站</span>
          <span class="info">{{leave_station}}</span>
        </div>
        <div class="infoItem">
          <span class="field">装车分公司</span>
          <span class="info">{{loading_branch}}</span>
        </div>
        <div class="infoItem">
          <span class="field">装车站</span>
          <span class="info">{{loading_station}}</span>
        </div>
        <div class="infoItem">
          <span class="field">上门取货</span>
          <span class="info">{{door_loading}}</span>
        </div>
        <div class="infoItem">
          <span class="field">装货方式</span>
          <span class="info">{{loading_mode}}</span>
        </div>
        <div class="infoItem">
          <span class="field">发运时间</span>
          <span class="info">{{departure_time | formatDate}}</span>
        </div>
      </van-tab>
      <van-tab title="收货人" class="infoGroup">
        <div class="infoItem">
          <span class="field">收货人</span>
          <span class="info">{{receiver_name}}</span>
        </div>
        <div class="infoItem">
          <span class="field">手机号</span>
          <span class="info">{{receiver_phone}}</span>
        </div>
        <div class="infoItem">
          <span class="field">到达分公司</span>
          <span class="info">{{arrave_branch}}</span>
        </div>
        <div class="infoItem">
          <span class="field">到站</span>
          <span class="info">{{arrival_station}}</span>
        </div>
        <div class="infoItem">
          <span class="field">卸货方式</span>
          <span class="info">{{unload_mode}}</span>
        </div>
        <div class="infoItem">
          <span class="field">上门卸货</span>
          <span class="info">{{door_unload}}</span>
        </div>
      </van-tab>
      <van-tab title="货物信息" class="infoGroup">
        <div class="infoItem">
          <span class="field">发电箱箱号</span>
          <span class="info">{{power_box_num}}</span>
        </div>
        <div v-for="i in goods_info" :key="i.goods_id" class="box-list">
          <div :class="i.show_detail? 'box-list-title':''">
            <span>{{i.box_num}} - {{i.train_num}}</span>
            <span>
              <van-icon name="arrow-up" v-show="i.show_detail" @click="showDetail(i)" />
              <van-icon name="arrow-down" v-show="!i.show_detail" @click="showDetail(i)" />
            </span>
          </div>
          <div v-show="i.show_detail">
            <span>货物名称：</span>
            <span>{{i.goods_name}}</span>
          </div>
          <div v-show="i.show_detail">
            <span>货物品类：</span>
            <span>{{getGoodsCategory(i.goods_category)}}</span>
          </div>
          <div v-show="i.show_detail">
            <span>冷藏箱设定温度（℃）：</span>
            <span>{{i.set_temp}}</span>
          </div>
          <div v-show="i.show_detail">
            <span>冷藏箱接收时间：</span>  
            <span>{{i.receive_time | formatDate}}</span>  
          </div>
          <div v-show="i.show_detail">
            <span>冷藏箱装车时间：</span>
            <span>{{i.loading_time | formatDate}}</span>
          </div>
        </div>
      </van-tab>
      <van-tab title="运单进度" class="infoGroup">
        <van-steps direction="vertical" :active="statusList.length-1">
          <van-step v-for="i in statusList" :key="i.status_id">
            <h3 class="stepListTitle">
              {{stepArr[+i.status].stepName}}{{i.station_name?'：'+i.station_name:''}}
              <span class="stepListOp">
                <van-icon name="edit" class="edit" @click="editStatue(i.status_id,i.status)" />
                <van-icon name="cross" class="delete" @click="deleteStatue(i.status_id)" />
              </span>
            </h3>
            <p  class="stepListTime">{{i.cross_station_time | formatDate}}</p>
          </van-step>
        </van-steps>
      </van-tab>
    </van-tabs>
    <div class="box-op">
      <van-button plain hairline round size="mini" type="danger" @click="deleteBill">删除</van-button>
    </div>
    <!-- 设置步骤内容 -->
    <van-popup v-model="showStepInfo" position="bottom" :close-on-click-overlay="overlay">
      <van-field required v-model="cross_station" v-show="clickStep==2" center clearable right-icon="question-o" label="过站" placeholder="请输入站名" @click-right-icon="$toast('请输入最少两位车站名称拼音首字母后点击选择过站按钮选择车站')">
        <van-button slot="button" :disabled="cross_station.length>1?false:true" size="small" type="primary" is-link @click="choiceCrossStation">选择过站</van-button>
      </van-field>
      <van-field v-model="step_time" required clearable disabled label="时间" placeholder="请输入时间" @click="choiceStepTime"/>
      <div class="box-op">
          <van-button plain hairline round size="mini" type="danger" @click="cancelSetStep">取消</van-button>
          <van-button plain hairline round size="mini" type="danger" @click="confirmSetStep">确认</van-button>
      </div>
    </van-popup>
    <!-- 过站列表 -->
    <van-popup v-model="showCrossStation" position="bottom" :close-on-click-overlay="overlay">
      <van-picker
        :columns="cross_stations"
        show-toolbar
        title="选择过站"
        @cancel="cancelCrossStation"
        @confirm="confirmCrossStation" />
    </van-popup>
    <!-- 步骤时间 -->
    <van-popup v-model="showStepTime" position="bottom" :close-on-click-overlay="overlay">
      <van-datetime-picker
        v-model="stepTime"
        type="datetime"
        @cancel="cancelStepTime"
        @confirm="confirmStepTime"
      />
    </van-popup>
  </div>
</template>

<script>
import stationList from '../js/stationData.js';
import goodsCategorys from '../js/goodsCategory.js';
import {formatDate } from '../js/formattime.js';
import {formatShowTime } from '../js/formatShowTime';
  export default {
    data() {
      return {
        overlay: false, //点击遮罩层隐藏
        activeStep: -1, //当前步骤
        activeAllStep: 2, //所有步骤
        clickStep: null, //点击步骤
        activeTab: 0, 
        travel_id: '', //运单id
        status_id: '', //状态id
        stepArr: [
          {'id':'0','stepName':'装货'},
          {'id':'1','stepName':'发车'},
          {'id':'2','stepName':'过站'},
          {'id':'3','stepName':'到站'},
          {'id':'4','stepName':'卸货'},
        ], //步骤列表
        goods_info: [{'train_num':'5301223','box_num':'TBJU1900168','goods_name':'冻鸡','goodsCategory':'速冻食品=>速冻水果','goods_category':'1.1','set_temp':'-18','receive_time':'18:14','loading_time':'18:16','show_detail':false},{'train_num':'5301224','box_num':'TBJU1900169','goods_name':'冻鸡','goodsCategory':'速冻食品=>速冻果汁','goods_category':'1.1','set_temp':'-18','receive_time':'18:14','loading_time':'18:16','show_detail':false}],
        statusList: [], //物流列表
        stationList: stationList, //车站列表
        goodsCategorys: goodsCategorys, //货物种类列表
        cross_station: '', //路过车站
        cross_stations: [], //路过车站列表
        showStepInfo: false, //显示步骤内容
        showCrossStation: false, //显示过站
        stepTime: new Date(), //步骤时间
        showStepTime: false, //显示步骤时间
        step_time: this.timeFormat(new Date()), //选中步骤时间
        title: '', //运单名
        shipper_name: '', //托运人
        leave_station: '', //发站
        shipper_phone: '', //托运手机号
        loading_branch: '', //装车分公司
        loading_mode: '', //装货方式
        door_loading: '', //上门取货
        loading_station: '', //装车站
        departure_time: '', //发运时间
        receiver_name: '', //收货人
        receiver_phone: '', //手机号
        arrave_branch: '', //到达分公司
        arrival_station: '', //到站
        unload_mode: '', //卸货方式
        door_unload: '', //上门卸货
        power_box_num: '', //发电箱箱号
      }
    },
    filters: {
      formatDate(time) {
        if(time != 0 && time != null){
          var date = new Date( time*1000);
          return formatDate(date, 'yyyy-MM-dd hh:mm');
        }else{
          return ''
        }        
      }
    },
    methods: {
      goBack() {
        this.$router.push('/transport')
      },
      changeNewBill(){
        this.$router.push({
          name: 'newway',
          query: {
            id: this.travel_id
          }
        })
      },
      // 显示隐藏冷藏箱详情
      showDetail(item){
        this.goods_info.find(v=>{v.box_num == item.box_num ? v.show_detail = !v.show_detail : ''})
      },
      setStep(i){
        if(i > this.activeStep){
          this.showStepInfo = true
          this.clickStep = +i 
        }else if(i == this.activeStep && i == 2){ 
          this.showStepInfo = true
          this.clickStep = +i
        }
      },
      // 步骤信息
      cancelSetStep(){
        this.stepTime = new Date(); 
        this.step_time = this.timeFormat(new Date()); 
        this.showStepInfo = false
      },
      confirmSetStep(){
        if(this.clickStep == 2 && this.cross_station == ''){
          this.$dialog.alert({
            title: '提示',
            message: '请选择车站！'
          })
          return false;
        }
        //保存信息
        this.$http.post('http://192.168.31.62:81/information/save_travel_info_status',{
          channel: 'app',
          travel_id: this.travel_id,
          cross_station_time: this.step_time,
          station_name: this.cross_station,
          status: this.clickStep,
          status_id: this.status_id
        },{
          headers: {'Token': localStorage.getItem('token')}
        }).then(res => {
          if(res.data.code == '200'){
            this.$toast('标记成功！');
            this.activeStep = this.clickStep;
            this.cross_station = '';
            this.status_id = '',
            this.getWaybillGoodsDetails();
          }else if(res.data.code == '401'){
            this.$dialog.alert({
              title: '提示',
              message: res.data.reason
            })
            this.$router.push({
              path: './me'
            })
          }else{
            this.$dialog.alert({
              title: '提示',
              message: '标记失败'+res.data.reason
            })
          }
        }).catch(err => {
          console.log(err)
        })
        this.showStepInfo = false
      },
      // 过站
      choiceCrossStation(){
        this.showCrossStation = true;
        this.cross_stations = [];
        this.stationList.find(v=>{
          if(v.pinyin.search(this.cross_station.toUpperCase()) != -1){
            this.cross_stations.push(v);
          }
        })
      },
      cancelCrossStation(){
        this.showCrossStation = false
      },
      confirmCrossStation(value){
        this.cross_station = value.text;
        this.showCrossStation = false;        
      },
      // 步骤时间
      choiceStepTime(){
        this.showStepTime = true;
      },
      cancelStepTime(){
        this.showStepTime = false;        
      },
      confirmStepTime(){
        this.showStepTime = false;
        this.step_time = this.timeFormat(this.stepTime)
      },      
      timeFormat(time) {
        let year = time.getFullYear();
        let month = time.getMonth() + 1;
        let day = time.getDate();
        let hour = time.getHours();
        hour = (hour+'').length==1?'0'+hour:hour
        let min = time.getMinutes();
        min = (min+'').length==1?'0'+min:min
        return year + '-' + month + '-' + day + ' ' + hour + ':' + min 
      },
      // 获取获取品类
      getGoodsCategory(i){
        var type = Object.keys(this.goodsCategorys)[Math.floor(+i)-1];
        var goodType = this.goodsCategorys[type][i.substr(i.length-1,1)-1]
        return type +'=>'+ goodType
      },
      // 获取运单信息
      getWaybillGoodsDetails(){
        this.$http.post('http://192.168.31.62:81/information/waybill_goods_details',{
          channel: 'app',
          travel_id: this.travel_id
        },{
          headers: {'Token': localStorage.getItem('token')}
        }).then(res => {
          if(res.data.code == '200'){
            this.activeStep = +res.data.result.last_status[0].status;
            this.title = res.data.result.travel[0].title;
            this.shipper_name = res.data.result.travel[0].shipper_name==null?'-':res.data.result.travel[0].shipper_name;
            this.leave_station = res.data.result.travel[0].leave_station==null?'-':res.data.result.travel[0].leave_station;
            this.shipper_phone = res.data.result.travel[0].shipper_phone==null?'-':res.data.result.travel[0].shipper_phone;
            this.arrave_branch = res.data.result.travel[0].arrave_branch==null?'-':res.data.result.travel[0].arrave_branch;
            this.loading_mode = res.data.result.travel[0].loading_mode==1?'冷库对位装车':res.data.result.travel[0].loading_mode==2?'站内冷柜装箱':'-';
            this.door_loading = res.data.result.travel[0].door_loading==1?'是':'否'
            this.loading_station = res.data.result.travel[0].loading_station;
            this.loading_branch = res.data.result.travel[0].loading_branch;
            this.departure_time = res.data.result.travel[0].departure_time;
            this.receiver_name = res.data.result.travel[0].receiver_name;
            this.receiver_phone = res.data.result.travel[0].receiver_phone;
            this.door_unload = res.data.result.travel[0].door_unload==1?'是':'否'
            this.arrival_station = res.data.result.travel[0].arrival_station;
            this.power_box_num = res.data.result.travel[0].power_box_num;
            this.unload_mode = res.data.result.travel[0].unload_mode==1?'冷库对位卸货':res.data.result.travel[0].unload_mode==2?'站内冷柜卸货':'-';
            res.data.result.goods.map(v=>{
              v.show_detail = false;
            });
            this.goods_info = res.data.result.goods
            this.statusList = res.data.result.status
          }else if(res.data.code == '401'){
            this.$dialog.alert({
              title: '提示',
              message: res.data.reason
            })
            this.$router.push({
              path: './me'
            })
          }else{
            this.$dialog.alert({
              title: '提示',
              message: '获取失败'+res.data.reason
            })
          }
        }).catch(err => {
          console.log(err)
        })
      },
      // 编辑物流信息
      editStatue(id,statue){
        this.status_id = id;
        this.clickStep = statue;
        this.showStepInfo = true;
      },
      deleteStatue(id){
        this.$dialog.confirm({
          title: '提示',
          message: '确定要删除此条及后面的标记么？'
        }).then(() => {
          this.$http.post('http://192.168.31.62:81/information/delete_travel_info_status',{
            channel: 'app',
            status_id: id
          },{
             headers: {'Token': localStorage.getItem('token')}
          }).then(res => {
            if(res.data.code == '200'){
              this.$toast('删除成功！');
              this.getWaybillGoodsDetails();
            }else if(res.data.code == '401'){
              this.$dialog.alert({
                title: '提示',
                message: res.data.reason
              })
              this.$router.push({
                path: './me'
              })
            }else{
              this.$dialog.alert({
                title: '提示',
                message: '删除失败'+res.data.reason
              })
            }
          }).catch(err => {
            console.log(err)
          });
        }).catch();
      },
      // 删除运单
      deleteBill(){
        this.$dialog.confirm({
          title: '提示',
          message: '确定要删除此运单么？'
        }).then(() => {
          this.$http.post('http://192.168.31.62:81/information/waybill_delete',{
            channel: 'app',
            travel_id: this.travel_id
          },{
             headers: {'Token': localStorage.getItem('token')}
          }).then(res => {
            if(res.data.code == '200'){
              this.$toast('删除成功！');
              this.$router.push({
                path: './transport'
              })
            }else if(res.data.code == '401'){
              this.$dialog.alert({
                title: '提示',
                message: res.data.reason
              })
              this.$router.push({
                path: './me'
              })
            }else{
              this.$dialog.alert({
                title: '提示',
                message: '删除失败'+res.data.reason
              })
            }
          }).catch(err => {
            console.log(err)
          });
        }).catch()
      }
    },
    mounted () {
      this.travel_id = this.$route.query.travel_id;
      if(this.travel_id == undefined){
        this.travel_id = '';
      }
      this.getWaybillGoodsDetails();
    },
  }
</script>

<style scoped>
#newbillDetail{
  height: 100%;
  padding-top: 46px;
}
.top{
  background-image: url('../assets/home-bg.png');
  height: 46px;
  background-position: center center;
  background-size: 100% auto; 
  position: fixed;
  top: 0;
  z-index: 100!important;
  width: 100%;
}
.top *{
  color: white!important;
}
.infoGroup{
  background-color: white;
  padding: 0 10px;
}
.infoTitle,.infoItem{
  border-top: 1px solid #eeeeee;
  font-size: 12px;
  padding: 10px 0;
  color: #555555;
}
.infoTitle{
  border: none;
  font-weight: bolder;
}
.infoItem{
  display: flex;
  justify-content: space-between;
}
.box-op{
  padding: 10px 0;
  display: flex;
  justify-content: space-around;
}
.box-list{
  margin: 10px;
  padding: 0 10px;
  background-color: white;
  color: #555555;
  font-size: 12px;
  display: block!important;
}
.box-list-title{
  border-bottom: 1px solid #dddddd;
}
.box-list>div{
  width: 100%;
  display: flex;
  justify-content:space-between;
  padding: 10px 0;
}
.stepListTitle{
  font-size: 14px;
  display: flex;
  justify-content: space-between;
}
.stepListTime{
  font-size: 12px;
}
.stepListOp .edit{
  color: #555555;
}
.stepListOp .delete{
  margin-left: 5px;
  color: red;
}
.box-op{
  padding: 10px 0;
  display: flex;
  justify-content: space-around;
}
</style>