<template>
  <div id="new-way">
    <van-nav-bar class="top"
      fixed
      title="新运单"
      left-text="返回"
      right-text="保存"
      left-arrow
      @click-left="goBack"
      @click-right="saveTravel"
    />
    <van-tabs v-model="activeTab" swipeable animated class="tab-top">
      <van-tab title="发货人" class="sender">
        <van-field v-model="title" clearable label="运单名称" placeholder="请输入运单名称"/>      
        <van-field v-model="shipper_name" clearable label="托运人" placeholder="请输入托运人"/>
        <van-field v-model="shipper_phone" @input="testPhone" label="手机号" placeholder="请输入手机号" :error-message="phoneErr==true?'手机号格式错误':''"/>
        <!-- <van-field v-model="loading_branch" disabled @click="choiceLoadingCompany" label="装车分公司" placeholder="请选择装车分公司"/> -->
        <van-field v-model="loading_station" required center clearable right-icon="question-o" label="装车站" placeholder="请输入站名" @click-right-icon="$toast('请输入最少两位车站名称拼音首字母后点击选择装车站钮选择车站')">
          <van-button slot="button" :disabled="loading_station.length>1?false:true" size="small" type="primary" is-link @click="choiceLoadingStation">选择装车站</van-button>
        </van-field>
        <van-field required v-model="leave_station" center clearable right-icon="question-o" label="发站" placeholder="请输入站名" @click-right-icon="$toast('请输入最少两位车站名称拼音首字母后点击选择发站按钮选择车站')">
          <van-button slot="button" :disabled="leave_station.length>1?false:true" size="small" type="primary" is-link @click="choiceSendStation">选择发站</van-button>
        </van-field>
        <div class="van-cell van-cell--center van-field">
          <div class="van-cell__title van-field__label">
            <span>装货方式</span>
          </div>
          <div class="van-cell__value">
            <van-radio-group v-model="loading_mode" class="door_loadingType">
              <van-radio name="1">冷库对位装箱</van-radio>
              <van-radio name="2">站内冷柜装箱</van-radio>
            </van-radio-group>
          </div>
        </div>
        <van-switch-cell v-model="door_loading" title="上门取货" />
        <van-field
          v-model="departure_time"
          required
          disabled
          label="发运时间"
          placeholder="选择发运时间"
          @click="choiceSendTime"
        >
        </van-field>
      </van-tab>
      <van-tab title="收货人" class="receive">
        <van-field v-model="receiver_name" clearable label="收货人" placeholder="请输入名称"/>
        <van-field v-model="receiver_phone" @input="testReceivePhone" label="手机号" placeholder="请输入手机号" :error-message="receiver_phoneErr==true?'手机号格式错误':''"/>
        <!-- <van-field v-model="arrave_branch" disabled @click="choiceArriveCompany" label="到达分公司" placeholder="请选择到达分公司"/> -->
        <van-field v-model="arrival_station" required center clearable right-icon="question-o" label="到站" placeholder="请输入站名" @click-right-icon="$toast('请输入最少两位车站名称拼音首字母后点击选择茶盏按钮选择车站')">
          <van-button slot="button" :disabled="arrival_station.length>1?false:true" size="small" type="primary" is-link @click="choiceRecevieStation">选择到站</van-button>
        </van-field>
        <div class="van-cell van-cell--center van-field">
          <div class="van-cell__title van-field__label">
            <span>卸货方式</span>
          </div>
          <div class="van-cell__value">
            <van-radio-group v-model="unload_mode" class="door_loadingType">
              <van-radio name="1">冷库对位卸箱</van-radio>
              <van-radio name="2">站内冷柜卸箱</van-radio>
            </van-radio-group>
          </div>
        </div>
        <van-switch-cell v-model="door_unload" title="上门卸货" />
      </van-tab>
      <van-tab title="货物信息" class="receive">
        <van-field v-model="power_box_num" required clearable label="发电箱箱号" placeholder="请输入发电箱箱号"/>
        <div v-for="i in goods_info" :key="i.box_num" class="box-list">
          <div :class="i.show_detail? 'box-list-title':''">
            <span>{{i.box_num}} - {{i.train_num}}</span>
            <span>
              <van-icon name="arrow-up" v-show="i.show_detail" @click="showDetail(i)" />
              <van-icon name="arrow-down" v-show="!i.show_detail" @click="showDetail(i)" />
              <van-icon name="edit" class="edit" @click="editList(i)"/>
              <van-icon name="cross" class="box-list-delete" @click="deleteList(i)" />
            </span>
          </div>
          <div v-show="i.show_detail">
            <span>货物名称：</span>
            <span>{{i.goods_name}}</span>
          </div>
          <div v-show="i.show_detail">
            <span>货物品类：</span>
            <span>{{i.goodsCategory}}</span>
          </div>
          <div v-show="i.show_detail">
            <span>冷藏箱设定温度（℃）：</span>
            <span>{{i.set_temp}}</span>
          </div>
          <div v-show="i.show_detail">
            <span>冷藏箱接收时间：</span>  
            <span>{{i.receive_time}}</span>  
          </div>
          <div v-show="i.show_detail">
            <span>冷藏箱装车时间：</span>
            <span>{{i.loading_time}}</span>
          </div>
        </div>
        <div class="add-box">
          <h3>添加冷藏箱</h3>
          <van-field v-model="train_num" required clearable label="车号" placeholder="请输入车号"/>
          <van-field v-model="box_num" required clearable label="箱号" placeholder="请输入箱号后四位" right-icon="qr" @click-right-icon="startRecognize()"/>
          <van-field v-model="goods_name" required clearable label="货物名称" placeholder="请输入货物名称"/>
          <van-field v-model="goodsCategory" required disabled label="货物品类" placeholder="请选择货物品类" @click="choiceGoodsCategory"/>
          <van-field v-model="set_temp" required clearable label="设定温度（℃）" placeholder="请输入设定温度"/>
          <van-field v-model="receive_timeValue" required clearable disabled label="接收时间" placeholder="请输入冷藏箱接收时间" @click="choiceReceiveBoxTime"/>
          <van-field v-model="loading_timeValue" required clearable disabled label="装车时间" placeholder="请输入冷藏箱装车时间" @click="chiceLoadingBoxTime"/>
          <div class="box-op">
            <van-button plain hairline round size="mini" type="danger" @click="addBox">添加</van-button>
          </div>
        </div>    
      </van-tab>
    </van-tabs>   
    <!-- 扫码 -->
    <div :class="[showScan ? 'toTop':'', 'scan']">
      <div id="bcid">
        <van-nav-bar class="top"
          fixed
          title="扫描箱号二维码"
          left-text="返回"
          left-arrow
          @click-left="cancel"
        />
        <div style="height:40%"></div>
        <p class="tip">...载入中...</p>
      </div>
    </div>
    <!-- 装车分公司 -->
    <!-- <van-popup v-model="showLoadingCompany" position="bottom">
      <van-picker 
        :columns="company"
        show-toolbar
        title="选择装车分公司"
        @cancel="cancelLoadingCompany"
        @confirm="confirmLoadingCompany"/>
    </van-popup> -->
     <!-- 装车站车站列表 -->
    <van-popup v-model="showLoadingStation" position="bottom">
      <van-picker
        :columns="loading_stations"
        show-toolbar
        title="选择装车站"
        @cancel="cancelLoadingStation"
        @confirm="confirmLoadingStation" />
    </van-popup>
    <!-- 发站车站列表 -->
    <van-popup v-model="showSendStation" position="bottom">
      <van-picker
        :columns="leave_stations"
        show-toolbar
        title="选择发站"
        @cancel="cancelSendStation"
        @confirm="confirmSendStation" />
    </van-popup>
    <!-- 到达分公司 -->
    <!-- <van-popup v-model="showArriveCompany" position="bottom">
      <van-picker 
        :columns="company"
        show-toolbar
        title="选择装车分公司"
        @cancel="cancelArriveCompany"
        @confirm="confirmArriveCompany"/>
    </van-popup> -->
    <!-- 到站车站列表 -->
    <van-popup v-model="showReceiveStation" position="bottom">
      <van-picker
        :columns="arrival_stations"
        show-toolbar
        title="选择发站"
        @cancel="cancelReceiveStation"
        @confirm="confirmReceiveStation" />
    </van-popup>
    <!-- 发运时间 -->
    <van-popup v-model="showSendTime" position="bottom">
      <van-datetime-picker
        v-model="sendTime"
        type="datetime"
        @cancel="cancelSendTime"
        @confirm="confirmSendTime"
      />
    </van-popup>
    <!-- 货物品类 -->
    <van-popup v-model="showGoodsCategory" position="bottom">
      <van-picker
        :columns="goodsCategoryList"
        show-toolbar
        value="1"
        title="货物品类"
        @change="changeGoodsCategoryList"
        @cancel="cancelGoodsCategoryList"
        @confirm="confirmGoodsCategoryList"
      />
    </van-popup>
    <!-- 接收时间 -->
    <van-popup v-model="showReceiveBoxTime" position="bottom">
      <van-datetime-picker
        v-model="receive_time"
        type="datetime"
        @cancel="cancelReceiveBoxTime"
        @confirm="confirmReceiveBoxTime"
      />
    </van-popup>
    <!-- 装车时间 -->
    <van-popup v-model="showLoadingBoxTime" position="bottom">
      <van-datetime-picker
        v-model="loading_time"
        type="datetime"
        @cancel="cancelLoadingBoxTime"
        @confirm="confirmLoadingBoxTime"
      />
    </van-popup>
  </div>
</template>

<script>
  let scan = null
  import {formatShowTime } from '../js/formatShowTime';
  import stationList from '../js/stationData.js';
  import areaList from '../js/area.js';
  import company from '../js/company.js';
  import goodsCategorys from '../js/goodsCategory.js';
  export default {
    data() {
      return {
        travel_id: '', //运单id
        activeTab: 0,
        stationList: stationList, //车站列表
        areaList, //城市列表
        company: company, //铁路公司列表
        goodsCategorys: goodsCategorys,
        goodsCategoryList: [
          {
            values: Object.keys(goodsCategorys)
          },
          {
            values: goodsCategorys['速冻食品']
          }
        ], //货物品类列表
        //托运人数据
        title: '', //运单名称
        shipper_name: '', //托运人名称
        shipper_phone: '',  //经办人电话
        phoneErr: false,  //经办人电话错误
        loading_mode: '',  //托运人装货方式
        // loading_branch: '', //装车分公司
        showLoadingCompany: false, //装车分公司弹框
        showLoadingStation: false, //装车站弹框
        loading_station: '', //装车站
        loading_stations: [], //装车站列表
        showSendStation: false, //发送站弹框
        leave_station: '', //发送站
        leave_stations: [], //发送站列表
        showSendTime: false, //发运时间弹框
        departure_time: '',  //发运时间
        sendTime: new Date(), //发运时间
        door_loading: false, //上门提货
        //收货人数据
        receiver_name: '', //收货人名称
        receiver_phone: '',  //经办人电话
        receiver_phoneErr: false,
        unload_mode: '',
        // arrave_branch: '', //装车分公司
        showArriveCompany: false, //装车分公司弹框
        showReceiveStation: false, //到站弹框
        arrival_station: '', //发送站
        arrival_stations: [], //发送站列表
        door_unload: false, //上门卸货
        //货物
        power_box_num: '', //发电箱箱号
        goods_info: [],
        train_num: '', //车号
        box_num: 'TBJU190', //箱号
        goods_name: '', //货物名称
        goodsCategory: '', //货物品类
        goods_category: '', //货物品类代码
        showGoodsCategory: false, //货物品类弹框
        set_temp: '', //设定温度
        //货物信息 
        loading_modeDelivery: '', //发站配送
        unload_modeDelivery: '', //到站配送
        showReceiveBoxTime: false, //接收时间弹框
        receive_time: new Date(), // 接收时间
        receive_timeValue: '', // 接收时间
        showLoadingBoxTime: false, //装车时间弹框
        loading_timeValue: '', //装车时间
        loading_time: new Date(), //装车时间
        showScan: false
      }
    },
    methods: {
      goBack() {
        history.go(-1)
      },
      testPhone(value){
        if(value.length == 11){
          this.phoneErr = false
        }else{
          this.phoneErr = true
        }
      },
      testReceivePhone(value){
        if(value.length == 11){
          this.receiver_phoneErr = false
        }else{
          this.receiver_phoneErr = true
        }
      },
      // 装车站
      choiceLoadingStation(){
        this.showLoadingStation = true;
        this.loading_stations = [];
        this.stationList.find(v=>{
          if(v.pinyin.search(this.loading_station.toUpperCase()) != -1){
            this.loading_stations.push(v);
          }
        })
      },
      cancelLoadingStation(){
        this.showLoadingStation = false;
      },
      confirmLoadingStation(value){
        if(value.length != 0){
          this.loading_station = value.text;
        }
        this.showLoadingStation = false;        
      },
      // 发站
      choiceSendStation(){
        this.showSendStation = true;
        this.leave_stations = [];
        this.stationList.find(v=>{
          if(v.pinyin.search(this.leave_station.toUpperCase()) != -1){
            this.leave_stations.push(v);
          }
        })
      },
      cancelSendStation(){
        this.showSendStation = false;
      },
      confirmSendStation(value){
        if(value.length != 0){
          this.leave_station = value.text;
        }
        this.showSendStation = false;
      },
      // 发运时间
      choiceSendTime(){
        this.showSendTime = true;
      },
      cancelSendTime(){
        this.showSendTime = false;
      },
      confirmSendTime(){
        this.showSendTime = false;
        this.departure_time = this.timeFormat(this.sendTime)
      },      
      timeFormat(time) {
        let year = time.getFullYear();
        let month = time.getMonth() + 1;
        let day = time.getDate();
        let hour = time.getHours();
        let min = time.getMinutes();
        min = (min+'').length==1?'0'+min:min
        return year + '-' + month + '-' + day + ' ' + hour + ':' + min        
      },
      //到站
      choiceRecevieStation(){
        this.showReceiveStation = true;
        this.arrival_stations = [];
        this.stationList.find(v=>{
          if(v.pinyin.search(this.arrival_station.toUpperCase()) != -1){
            this.arrival_stations.push(v);
          }
        })
      },
      cancelReceiveStation(){
        this.showReceiveStation = false;
      },
      confirmReceiveStation(value){
        if(value.length != 0){
          this.arrival_station = value.text;
        }
        this.showReceiveStation = false;
      },
      // 显示隐藏冷藏箱详情
      showDetail(item){
        this.goods_info.find(v=>{v.box_num == item.box_num ? v.show_detail = !v.show_detail : ''})
      },
      // 删除箱子
      deleteList(item){
        this.$dialog.confirm({
          title: '确认',
          message: '确定要删除这条信息吗？'
        }).then(() => {
          this.goods_info.map((v,i)=>{
            if(v.box_num == item.box_num){
              this.goods_info.splice(i,1)
            }
          })
        }).catch(() => {
          // on cancel
        });
      },
      // 编辑箱子
      editList(item){
        this.train_num = item.train_num
        this.box_num = item.box_num
        this.goods_name = item.goods_name
        this.goodsCategory = item.goodsCategory
        this.set_temp = item.set_temp
        this.receive_timeValue = item.receive_time
        this.loading_timeValue = item.loading_time
        this.goods_info.map((v,i)=>{
          if(v.box_num == item.box_num){
            this.goods_info.splice(i,1)
          }
        })
      },
      // 冷藏箱接收时间
      choiceReceiveBoxTime(){
        this.showReceiveBoxTime = true
      },
      cancelReceiveBoxTime(){
        this.showReceiveBoxTime = false
      },
      confirmReceiveBoxTime(value){
        this.receive_timeValue = this.timeFormat(this.receive_time)
        this.showReceiveBoxTime = false
      },
      // 冷场箱装车时间
      chiceLoadingBoxTime(){
        this.showLoadingBoxTime = true;
      },
      cancelLoadingBoxTime(){
        this.showLoadingBoxTime = false
      },
      confirmLoadingBoxTime(){
        this.loading_timeValue = this.timeFormat(this.loading_time)
        this.showLoadingBoxTime = false
      },
      // 选择货物品类
      choiceGoodsCategory(){
        this.showGoodsCategory = true;
      },
      changeGoodsCategoryList(picker,values){
        picker.setColumnValues(1, goodsCategorys[values[0]]);    
      },
      cancelGoodsCategoryList(){
        this.showGoodsCategory = false;
      },
      confirmGoodsCategoryList(picker,value){
        this.goodsCategory = picker[0] + ' > ' + picker[1];
        this.goods_category = ++value[0] + '.' + ++value[1];
        this.showGoodsCategory = false;
      },
      addBox(){
        if(!this.train_num || !this.box_num || !this.goods_name || !this.set_temp || !this.receive_timeValue || !this.loading_timeValue){
          this.$dialog({
            title: '提示',
            message: '信息不能为空！' 
          });
        }else if(this.box_num.length != 11){
          this.$dialog({
            title: '提示',
            message: '请输入正确箱号!' 
          });
        }else if(this.train_num.length != 7){
          this.$dialog({
            title: '提示',
            message: '请输入七位车号！' 
          });
        }else{          
          if(this.goods_info.length == 0){
            this.goods_info.push({
              'goods_id': '',
              'train_num': this.train_num,
              'box_num': this.box_num,
              'goods_name': this.goods_name,
              'goodsCategory': this.goodsCategory,
              'goods_category': this.goods_category,
              'set_temp': this.set_temp,
              'receive_time': this.receive_timeValue,
              'loading_time': this.loading_timeValue,
              'show_detail': false
            })
          }else{
            this.goods_info.map((v,i)=>{
              if(v.box_num == this.box_num){
                this.$dialog({
                  title: '提示',
                  message: '箱号已存在' 
                });
              }else if(v.train_num == this.train_num){
                this.$dialog({
                  title: '提示',
                  message: '车号已存在' 
                });
              }else{
                 this.goods_info.push({
                  'goods_id': '',
                  'train_num': this.train_num,
                  'box_num': this.box_num,
                  'goods_name': this.goods_name,
                  'goodsCategory': this.goodsCategory,
                  'goods_category': this.goods_category,
                  'set_temp': this.set_temp,
                  'receive_time': this.receive_timeValue,
                  'loading_time': this.loading_timeValue,
                  'show_detail': false
                })
              }
            })                   
          }
          this.train_num = '';
          this.box_num = 'TBJU190'
        }        
      },
      saveTravel(){
        if(this.leave_station == ''){
          this.$dialog.alert({
            title: '提示', 
            message: '请选择发站！'
          })
        }else if(this.arrival_station == ''){
           this.$dialog.alert({
            title: '提示',
            message: '请选择到站！'
          })
        }else if(this.departure_time == ''){
          this.$dialog.alert({
            title: '提示',
            message: '请选择发运时间！'
          })
        }else if(this.power_box_num == ''){
          this.$dialog.alert({
            title: '提示',
            message: '请添加发电箱号！'
          })
        }else if(this.goods_info.length == 0){
          this.$dialog.alert({
            title: '提示',
            message: '请添加货物信息！'
          })
        }else{
          this.$http.post('http://192.168.31.62:81/information/save_waybill',{
            channel: 'app',
            travel_id: this.travel_id,
            title: this.title,
            shipper_name: this.shipper_name,
            shipper_phone: this.shipper_phone,
            loading_station: this.loading_station,
            leave_station: this.leave_station,
            loading_mode: this.loading_mode,
            door_loading: this.door_loading,
            departure_time: this.departure_time,
            receiver_name: this.receiver_name,
            receiver_phone: this.receiver_phone,
            arrival_station: this.arrival_station,
            unload_mode: this.unload_mode,
            door_unload: this.door_unload,
            power_box_num: this.power_box_num,
            goods_info: JSON.stringify(this.goods_info)
          },{
            headers: {'Token': localStorage.getItem('token')}
          }).then(res => {
            if(res.data.code == '200'){
              this.$toast('保存成功！');
              this.$router.push({
                name:'newbillDetail',
                query: {
                  travel_id: res.data.result
                }
              })
            }else if(res.data.code == '401'){
              this.$dialog.alert({
                title: '提示',
                message: res.data.reason
              })
              this.$router.push({
                path: './me'
              })
            }else{
              this.$dialog.alert({
                title: '提示',
                message: '保存失败：'+res.data.reason
              })
            }
          }).catch(err => {
            console.log(err)
          })
        }
      },
      getGoodsCategory(i){
        var type = Object.keys(this.goodsCategorys)[Math.floor(+i)-1];
        var goodType = this.goodsCategorys[type][i.substr(i.length-1,1)-1]
        return type +'=>'+ goodType
      },
      // 获取运单信息
      getWaybillGoodsDetails(){
        this.$http.post('http://192.168.31.62:81/information/waybill_goods_details',{
          channel: 'app',
          travel_id: this.travel_id
        },{
          headers: {'Token': localStorage.getItem('token')}
        }).then(res => {
          if(res.data.code == '200'){
            this.activeStep = +res.data.result.last_status[0].status;
            this.title = res.data.result.travel[0].title;
            this.shipper_name = res.data.result.travel[0].shipper_name
            this.leave_station = res.data.result.travel[0].leave_station
            this.shipper_phone = res.data.result.travel[0].shipper_phone
            this.arrave_branch = res.data.result.travel[0].arrave_branch
            this.loading_mode = res.data.result.travel[0].loading_mode
            this.door_loading = res.data.result.travel[0].door_loading=='1'?true:false
            this.loading_station = res.data.result.travel[0].loading_station||'';
            this.loading_branch = res.data.result.travel[0].loading_branch;
            this.departure_time = formatShowTime(res.data.result.travel[0].departure_time);
            this.receiver_name = res.data.result.travel[0].receiver_name;
            this.receiver_phone = res.data.result.travel[0].receiver_phone;
            this.door_unload = res.data.result.travel[0].door_unload=='1'?true:false
            this.arrival_station = res.data.result.travel[0].arrival_station||'';
            this.power_box_num = res.data.result.travel[0].power_box_num;
            this.unload_mode = res.data.result.travel[0].unload_mode
            res.data.result.goods.map(v=>{
              v.show_detail = false;
              v.goodsCategory = this.getGoodsCategory(v.goods_category)
            });
            this.goods_info = res.data.result.goods
            this.statusList = res.data.result.status
          }else if(res.data.code == '401'){
            this.$dialog.alert({
              title: '提示',
              message: res.data.reason
            })
            this.$router.push({
              path: './me'
            })
          }else{
            this.$dialog.alert({
              title: '提示',
              message: '获取失败'+res.data.reason
            })
          }
        }).catch(err => {
          console.log(err)
        })
      },
      cancel(){
        this.showScan = false;
        this.closeScan()
        this.cancelScan()
        scan = null
      },
      // 创建扫描控件
      startRecognize () {     
        this.showScan = true;   
        let that = this
        if (!window.plus) return
        scan = new plus.barcode.Barcode('bcid')
        scan.onmarked = onmarked
        scan.start()
        function onmarked (type, result, file) {
          switch (type) {
            case plus.barcode.QR:
              type = 'QR'
              break
            case plus.barcode.EAN13:
              type = 'EAN13'
              break
            case plus.barcode.EAN8:
              type = 'EAN8'
              break
            default:
              type = '其它' + type
              break
          }
          result = result.replace(/\n/g, '')
          that.box_num = result
          that.showScan = false;
          that.closeScan()
          that.cancelScan()
          scan = null
        }
      },
      // 开始扫描
      startScan () {        
        if (!window.plus) return
        scan.start()
      },
      // 关闭扫描
      cancelScan () {
        if (!window.plus) return
        scan.cancel()
      },
      // 关闭条码识别控件
      closeScan () {
        if (!window.plus) return
        scan.close()
      },
    },
    mounted () {
      this.travel_id = this.$route.query.id
      if(this.travel_id == undefined){
        this.travel_id = '';
      }
      this.getWaybillGoodsDetails()
    }
  }
</script>

