<template>
  <div id="deliver">
    <van-nav-bar class="top"
      fixed
      title="站外派送"
      left-text="返回"
      :right-text="showAdd?'保存':'修改'"
      left-arrow
      @click-left="goBack"
      @click-right="change"
    />
    <div class="add" v-show="showAdd">
      <van-field v-model="handle_station" required center clearable right-icon="question-o" label="办理站" placeholder="请输入站名" @click-right-icon="$toast('请输入最少两位车站名称拼音首字母后点击选择办理站钮选择车站')">
        <van-button slot="button" :disabled="handle_station.length>1?false:true" size="small" type="primary" is-link @click="choiceHandleStation">选择办理站</van-button>
      </van-field>
      <van-field v-model="delivery_company" clearable label="交付单位" placeholder="请输入交付单位"/>
      <van-field
        v-model="operation_time"
        disabled
        label="交接时间"
        placeholder="选择交接时间"
        @click="choiceDeliverTime"
      />
      <van-field v-model="accept_company" clearable label="接收单位" placeholder="请输入接收单位"/>
      <van-field
        v-model="departure_time"
        disabled
        label="出站时间"
        placeholder="选择出站时间"
        @click="choiceOutTime"
      />
      <div v-for="i in box_info" :key="i.box_num" class="box-list">
        <div :class="i.show_detail? 'box-list-title':''">
          <span>{{i.box_num}}</span>
          <span>
            <van-icon name="arrow-up" v-show="i.show_detail" @click="showDetail(i)" />
            <van-icon name="arrow-down" v-show="!i.show_detail" @click="showDetail(i)" />
            <van-icon name="edit" class="edit" @click="editList(i)"/>
            <van-icon name="cross" class="box-list-delete" @click="deleteList(i)" />
          </span>
        </div>
        <div v-show="i.show_detail">
          <span>出站温度：</span>
          <span>{{i.outgoing_temp}}</span>
        </div>
        <div v-show="i.show_detail">
          <span>搬出汽车号：</span>
          <span>{{i.move_car_num}}</span>
        </div>        
      </div>
      <div class="add-box">
        <h3>添加冷藏箱</h3>
        <van-field v-model="box_num" required clearable label="箱号" placeholder="请输入箱号" right-icon="qr" @click-right-icon="startRecognize()"/>
        <van-field v-model="move_car_num" clearable label="搬出汽车号" placeholder="请输入搬出汽车号"/>
        <van-field v-model="outgoing_temp" clearable label="出站温度（℃）" placeholder="请输入出站温度"/>    
        <div class="box-op">
          <van-button plain hairline round size="mini" type="danger" @click="addBox">添加箱子</van-button>
        </div>  
      </div>
    </div>
    <div class="show" v-show="!showAdd">
      <div class="infoItem">
        <span class="field">办理站</span>
        <span class="info">{{handle_station}}</span>
      </div>
      <div class="infoItem">
        <span class="field">交付单位</span>
        <span class="info">{{delivery_company}}</span>
      </div>
      <div class="infoItem">
        <span class="field">接收单位</span>
        <span class="info">{{accept_company}}</span>
      </div>
      <div class="infoItem">
        <span class="field">出站时间</span>
        <span class="info">{{departure_time}}</span>
      </div>
      <div class="infoItem">
        <span class="field">	交接时间</span>
        <span class="info">{{operation_time}}</span>
      </div>
      <div v-for="i in box_info" :key="i.box_num" class="box-list">
        <div :class="i.show_detail? 'box-list-title':''">
          <span>{{i.box_num}}</span>
          <span>
            <van-icon name="arrow-up" v-show="i.show_detail" @click="showDetail(i)" />
            <van-icon name="arrow-down" v-show="!i.show_detail" @click="showDetail(i)" />
          </span>
        </div>
        <div v-show="i.show_detail">
          <span>出站温度：</span>
          <span>{{i.outgoing_temp}}</span>
        </div>
        <div v-show="i.show_detail">
          <span>搬出汽车号：</span>
          <span>{{i.move_car_num}}</span>
        </div>        
      </div>
      <div class="box-op">
        <van-button plain hairline round size="mini" type="danger" @click="deleteYard">删除</van-button>
      </div>
    </div>
    <!-- 扫码 -->
    <div :class="[showScan ? 'toTop':'', 'scan']">
      <div id="bcid">
        <van-nav-bar class="top"
          fixed
          title="扫描箱号二维码"
          left-text="返回"
          left-arrow
          @click-left="cancel"
        />
        <div style="height:40%"></div>
        <p class="tip">...载入中...</p>
      </div>
    </div>
    <!-- 办理站列表 -->
    <van-popup v-model="showHandleStation" position="bottom">
      <van-picker
        :columns="handle_stations"
        show-toolbar
        title="选择办理站"
        @cancel="cancelHandleStation"
        @confirm="confirmHandleStation" />
    </van-popup>
    <!-- 交接时间 -->
    <van-popup v-model="showDeliverTime" position="bottom">
      <van-datetime-picker
        v-model="deliverTime"
        type="datetime"
        @cancel="cancelDeliverTime"
        @confirm="confirmDeliverTime"
      />
    </van-popup>
    <!-- 出站时间 -->
    <van-popup v-model="showOutTime" position="bottom">
      <van-datetime-picker
        v-model="outTime"
        type="datetime"
        @cancel="cancelOutTime"
        @confirm="confirmOutTime"
      />
    </van-popup>
  </div>
</template>

<script>
  let scan = null
  import {formatShowTime } from '../js/formatShowTime';
  import stationList from '../js/stationData.js';
  import {formatDate } from '../js/formattime.js';
  export default {
    data() {
      return {
        list_id: '',
        showAdd: false, //显示添加
        stationList: stationList, //车站列表
        handle_station: '', //办理站
        handle_stations: [], //办理站列表
        showHandleStation: false, //办理站弹框
        delivery_company: '', //交付单位
        operation_time: '', //交接时间
        deliverTime: new Date(), //交接时间
        showDeliverTime: false, //选择交接时间
        accept_company: '', //接收单位
        move_car_num: '', //搬出汽车号
        departure_time: '', //出站时间
        outTime: new Date(), //出站时间
        showOutTime: false, //选择出站时间
        outgoing_temp: '', //出站温度
        box_num: 'TBJU190', //箱号
        box_id: '',
        box_info: [], //设备数据
        showScan: false
      }
    },
    filters: {
      formatDate(time) {
        if(time != 0 && time != null){
          var date = new Date( +time*1000);
          return formatDate(date, 'yyyy-MM-dd hh:mm');
        }else{
          return ''
        }        
      }
    },
    methods: {
      goBack() {
        history.go(-1)
      },
      timeFormat(time) {
        let year = time.getFullYear();
        let month = time.getMonth() + 1;
        let day = time.getDate();
        let hour = time.getHours();
        let min = time.getMinutes();
        min = (min+'').length==1?'0'+min:min
        return year + '-' + month + '-' + day + ' ' + hour + ':' + min  
      },
      change(){
        if(this.showAdd == false){
          this.showAdd = true
        }else{
          this.saveTravel()
        }
      },
      showDetail(item){
        this.box_info.find(v=>{v.box_num == item.box_num ? v.show_detail = !v.show_detail : ''})
      },
      editList(item){
        this.box_id = item.box_id
        this.box_num = item.box_num
        this.outgoing_temp = item.outgoing_temp
        this.move_car_num = item.move_car_num        
        this.box_info.map((v,i)=>{
          if(v.box_num == item.box_num){
            this.box_info.splice(i,1)
          }
        })
      },
      // 选择办理站
      choiceHandleStation(){
        this.handle_stations = [];
        this.stationList.find(v=>{
          if(v.pinyin.search(this.handle_station.toUpperCase()) != -1){
            this.handle_stations.push(v);
          }
        })
        this.showHandleStation = true;
      },
      cancelHandleStation(){
        this.showHandleStation = false;
      },
      confirmHandleStation(value){
        if(value.length != 0){
          this.handle_station = value.text;
        }
        this.showHandleStation = false;
      },
      // 交接时间
      choiceDeliverTime(){
        this.showDeliverTime = true
      },
      cancelDeliverTime(){
        this.showDeliverTime = false
      },
      confirmDeliverTime(){
        this.showDeliverTime = false;
        this.operation_time = this.timeFormat(this.deliverTime)
      },
      // 出站时间
      choiceOutTime(){
        this.showOutTime = true
      },
      cancelOutTime(){
        this.showOutTime = false
      },
      confirmOutTime(){
        this.showOutTime = false;
        this.departure_time = this.timeFormat(this.outTime)
      },
      // 删除箱子
      deleteList(item){
        this.$dialog.confirm({
          title: '确认',
          message: '确定要删除这条信息吗？'
        }).then(() => {
          this.$http.post('http://192.168.31.62:81/information/delete_box_info',{
            'channel': 'app',
            'box_id': item.box_id
          },{
            headers: {
              'Token': localStorage.getItem('token'),
            }
          })
        }).then(res => {
          if(res.data.code == 200){
            this.$toast('删除成功');
            this.box_info.map(v => {
              if(v.box_id == item.box_id){
                this.box_info.splice(i,1)
              }
            })
          }else if(res.data.code == '401'){
            this.$dialog.alert({
              title: '提示',
              message: res.data.reason
            })
            this.$router.push({
              path: './me'
            })
          }else{
            this.$toast('删除失败'+res.date.reason);
          }
        }).catch(err => {
          console.log(err)
        })
      },
      addBox(){
        if(this.box_num.length != 11){
          this.$dialog({
            title: '提示',
            message: '请输入11位箱号!' 
          });
        }else{          
          if(this.box_info.length == 0){            
            this.box_info.push({
              'box_id': this.box_id,
              'box_num': this.box_num,
              'outgoing_temp': this.outgoing_temp,
              'move_car_num': this.move_car_num,
              'show_detail':false
            })
          }else{
            let have = false;  
            this.box_info.find((v,i)=>{
              if(v.box_num == this.box_num){
                have = true
                this.$dialog({
                  title: '提示',
                  message: '箱号已存在!' 
                });
              }
            })
            if(!have){
              this.box_info.push({
                'box_id': this.box_id,
                'box_num': this.box_num,
                'outgoing_temp': this.outgoing_temp,
                'move_car_num': this.move_car_num,
                'show_detail':false
              })
            }
          }
        }
        this.box_num = 'TBJU190',
        this.box_id = '';
      },
      saveTravel(){        
        if(this.handle_station == ''){
          this.$dialog({
            title: '提示',
            message: '请添加必填项！'
          });
        }else if(this.box_info.length == 0){
           this.$dialog({
            title: '提示',
            message: '至少添加一个冷藏箱！' 
          });
        }else{
          this.$http.post('http://192.168.31.62:81/information/save_operation_list',{
            channel: 'app',
            list_id: this.list_id,
            list_type: 2,
            handle_station: this.handle_station,
            delivery_company: this.delivery_company,
            accept_company: this.accept_company,
            departure_time: this.departure_time,
            operation_time: this.operation_time,
            box_info: JSON.stringify(this.box_info)
          },{
            headers: {'Token': localStorage.getItem('token')}
          }).then(res => {
            if(res.data.code == 200){
              this.$toast('保存成功');
              this.list_id = res.data.result;
              this.getOperationDetail();
              this.showAdd = false
            }else if(res.data.code == '401'){
              this.$dialog.alert({
                title: '提示',
                message: res.data.reason
              })
              this.$router.push({
                path: './me'
              })
            }
          }).catch(err => {
            console.log(err)
          })
        }
      },
      getOperationDetail(){
        this.$http.post('http://192.168.31.62:81/information/operation_list_details',{
          channel: 'app',
          list_id: this.list_id
        },{
          headers: {'Token': localStorage.getItem('token')}
        }).then(res => {
          if(res.data.code == 200){
            this.handle_station = res.data.result.operation[0].handle_station;
            this.operation_time = formatShowTime(res.data.result.operation[0].operation_time);
            this.delivery_company = res.data.result.operation[0].delivery_company;
            this.accept_company = res.data.result.operation[0].accept_company;
            this.departure_time = formatShowTime(res.data.result.operation[0].departure_time);
            res.data.result.box.map(v => {
              v.show_detail = false
            })
            this.box_info = res.data.result.box;
          }else if(res.data.code == '401'){
            this.$dialog.alert({
              title: '提示',
              message: res.data.reason
            })
            this.$router.push({
              path: './me'
            })
          }else{
            this.$dialog.alert({
              title: '提示',
              message: '获取失败'+res.data.reason
            })
          }
        }).catch(err => {
          console.log(err)
        })
      },
      cancel(){
        this.showScan = false;
        this.closeScan()
        this.cancelScan()
        scan = null
      },
      // 创建扫描控件
      startRecognize () {
        this.showScan = true;   
        let that = this
        if (!window.plus) return
        scan = new plus.barcode.Barcode('bcid')
        scan.onmarked = onmarked
        scan.start()
        function onmarked (type, result, file) {
          switch (type) {
            case plus.barcode.QR:
              type = 'QR'
              break
            case plus.barcode.EAN13:
              type = 'EAN13'
              break
            case plus.barcode.EAN8:
              type = 'EAN8'
              break
            default:
              type = '其它' + type
              break
          }
          result = result.replace(/\n/g, '')
          that.box_num = result
          that.showScan = false;
          that.closeScan()
          that.cancelScan()
          scan = null
        }
      },
      // 开始扫描
      startScan () {        
        if (!window.plus) return
        scan.start()
      },
      // 关闭扫描
      cancelScan () {
        if (!window.plus) return
        scan.cancel()
      },
      // 关闭条码识别控件
      closeScan () {
        if (!window.plus) return
        scan.close()
      },
      // 删除派送
      deleteYard(){
        this.$dialog.confirm({
          title: '提示',
          message: '确定要删除此条记录么？'
        }).then(() => {
          this.$http.post('http://192.168.31.62:81/information/operation_list_delete',{
            channel: 'app',
            list_id: this.list_id
          },{
             headers: {'Token': localStorage.getItem('token')}
          }).then(res => {
            if(res.data.code == '200'){
              this.$toast('删除成功！');
              this.$router.push({
                path: './transport'
              })
            }else if(res.data.code == '401'){
              this.$dialog.alert({
                title: '提示',
                message: res.data.reason
              })
              this.$router.push({
                path: './yard'
              })
            }else{
              this.$dialog.alert({
                title: '提示',
                message: '删除失败'+res.data.reason
              })
            }
          }).catch(err => {
            console.log(err)
          });
        }).catch()
      }
    },
    mounted () {
      this.list_id = this.$route.query.list_id
      if(this.list_id == undefined){
        this.list_id = '';
        this.showAdd = true
      }else{
        this.getOperationDetail()
      }
    },
  }
</script>

<style scoped>
#deliver{
  width: 100%;
  min-height: 100%;
  padding-top: 46px;
}
.top{
  background-image: url('../assets/home-bg.png');
  height: 46px;
  background-position: center center;
  background-size: 100% auto; 
  position: fixed;
  top: 0;
  z-index: 100;
  width: 100%;
}
.top *{
  color: white!important;
}
.box-list{
  margin: 10px;
  padding: 0 10px;
  background-color: white;
  color: #555555;
  font-size: 12px;
  display: block!important;
}
.box-list-title{
  border-bottom: 1px solid #dddddd;
}
.box-list>div{
  width: 100%;
  display: flex;
  justify-content:space-between;
  padding: 10px 0;
}
.box-list>div>span:first-child{
  min-width: 25%;
}
.box-list-delete{
  color: red;
  margin-left: 20px;
}
.edit{
  margin-left: 20px;
}
.add-box{
  background-color: white;
}
.add-box h3{
  padding: 10px;
  font-size: 14px;
  font-weight: bold;
}
.box-op{
  padding: 10px 0;
  display: flex;
  justify-content: space-around;
}
.box-op button{
  padding: 0 5px!important;
}
/* 显示 */
.infoItem{
  border-top: 1px solid #eeeeee;
  font-size: 12px;
  padding: 0 10px;
  color: #555555;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  background-color: white;
}

/* 扫码 */
.scan {
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
}
.toTop{
  z-index: 100000;
}
.scan #bcid {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 3rem;
  text-align: center;
  color: #fff;
  background: #ccc;
}
#bcid .top{
  z-index: 1000000!important;
}
.scan footer {
  position: absolute;
  left: 0;
  bottom: 1rem;
  height: 2rem;
  line-height: 2rem;
  z-index: 2;
}
</style>